/* MENU STYLES */
.menu{
  background-color: rgb(156,190,139);
  box-shadow: 0px 20px 30px rgb(53, 53, 40);   
  display: inline-flex;
  position: fixed;
  z-index: 4;
  justify-content: space-evenly;
  border-bottom: 5px solid black ;
  width: 100vw;
  height:max-content;
  margin:0;
 
}
.contactInfo a:link{
  font-size: 3vmin;
  text-decoration: none;
  color:black;
  padding:2vmin;
  align-content: center;
  font-weight:bold;
}



.option, .option a{
  display: block;
  text-align: center;
}


.icon, .option a{
  color:black;
  font-size: 5vmax;
 
}

.description{
  font-size: 2vmin;
  color:rgb(63,83,53);
  margin:0;
  font-weight: bold;
}